import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { RegisterProvider } from './../../providers/register/register';

/**
 * Generated class for the CityPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-city',
    templateUrl: 'city.html',
})

export class CityPage {

    cities: Array<any> = [];
    locations: Array<any> = [];
    city: number;

    constructor(public navCtrl: NavController, 
                public navParams: NavParams,
                private regPro: RegisterProvider,
                public viewCtrl: ViewController) {

        this.city = navParams.get('city') || 0;
        this.locations = navParams.get('locations') || [];

        this.cities = [
            {
                id: 1,
                name: " (Moscow)"
            },
            {
                id: 2,
                name: " (Jakarta)"
            },
            {
                id: 3,
                name: " (Seoul, Incheon)"
            },
            {
                id: 4,
                name: " (Xinbei)"
            },
            {
                id: 5,
                name: " (Beijing, Shanghai)"
            },
            {
                id: 6,
                name: " Hokkaido  Tokyo  Nagoya  Osaka  Kyoto  Fukuoka"
            },
            {
                id: 7,
                name: " (Mexico City)"
            },
            {
                id: 8,
                name: " (Sao Paulo  Rio de Janeiro)"
            },
            {
                id: 9,
                name: " (Hongkong)"
            },
            {
                id: 10,
                name: " (Philadelphia)"
            },
            {
                id: 11,
                name: "  (Houston)"
            },
            {
                id: 12,
                name: " (Chicago)"
            },
            {
                id: 13,
                name: " (Los Angeles)"
            },
            {
                id: 14,
                name: " (Hongkong)"
            },
            {
                id: 15,
                name: " (Toronto))"
            }
        ]
    }

    dismiss() {
        let data = { 'city': this.city };
        this.viewCtrl.dismiss(data);
    }

    ionViewDidLoad() {
        
    }

}



